.formBox{
  height: 100vh;
  padding-top: 60px;
  padding-left: 40px;
  background-image: linear-gradient(
    rgba(255, 255, 255, 0.8),
    rgba(0, 0, 0, 0.8)
  ),
  url(img/6.jpg);
}
.form {
    width: 320px;
  }

.label {
    display: flex;
    flex-direction: column;
    gap:4px;
    margin-bottom: 16px;
    color: rgba(196, 88, 88, 0.75);
    text-transform: uppercase;
    font-weight: bold;
  }

  .formButton {
    margin-top: 10px;
    border-radius: 5px;
    padding-block: 7px;
    padding-inline: 18px;
    cursor: pointer;
    border-color: aliceblue;
    background-color: rgb(239, 200, 200);
    color:rgba(196, 88, 88, 0.75);

    &:hover{
        background-color: rgba(196, 88, 88, 0.75);
        color:white;
    }

}

input {
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  width: 320px;
  border: 1px solid rgba(33, 33, 33, 0.3);
  border-radius: 4px;
  cursor: pointer;
}

input:focus {
  outline-color: rgba(196, 88, 88, 0.75);
}
